ader__text {\r\n    color: var(--272557, #272557);\r\n    font-family:'Poppins-400';\r\n    font-size: 14px;\r\n    line-height: 21px; \r\n    letter-spacing: 0.079px;\r\n    &.color {\r\n        color: var(--aebdda, #7791C2);\r\n    }\r\n}\r\n.postChat__header__sorting {\r\n    display: flex;\r\n    align-items: center;\r\n    column-gap: 10px;\r\n}\r\n.postChat__body {\r\n    width: 100%;\r\n    height: auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    margin-bottom: 43.5px;\r\n}\r\n.postChat__body__item {\r\n    position: relative;\r\n    width: 100%;\r\n    height: auto;\r\n    padding: 16px 29px;\r\n    display: flex;\r\n    align-items: flex-start;\r\n    column-gap: 22px;\r\n    &.background {\r\n     background: var(--f-8-faff, #F8FAFF);\r\n    }\r\n    // &.display {\r\n    //  //display: none;\r\n    // }\r\n}\r\n.item__imgWrapper {\r\n   width: 50px;\r\n   height: 50px;\r\n}\r\n.item__info {\r\n    width: 100%;\r\n    height: auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n.item__info__date {\r\n    display: flex;\r\n    align-items: center;\r\n    column-gap: 10px;\r\n    margin-bottom: 8px;\r\n}\r\n.info__date__likeWrapper {\r\n    display: flex;\r\n    align-items: center;\r\n    column-gap: 5px;\r\n    & .numbers,.text {\r\n    color: var(--aebdda, #7791C2);\r\n    font-family: 'Poppins-400';\r\n    font-size: 14px;\r\n    line-height: 21px; \r\n    letter-spacing: 0.079px;\r\n    }\r\n}\r\n.info__date__date {\r\n    color: var(--aebdda, #7791C2);\r\n    font-family: 'Poppins-400';\r\n    font-size: 14px;\r\n    line-height: 21px; \r\n    letter-spacing: 0.079px;\r\n    margin-left: 13px;\r\n}\r\n.item__info__description {\r\n    display: flex;\r\n    flex-direction: column;\r\n    margin-bottom: 32px;\r\n    & .title {\r\n    color: var(--272557, #272557);\r\n    font-family: 'Poppins-500';\r\n    font-size: 14px;\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    line-height: 20.4px; \r\n    letter-spacing: